import {Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef} from '@angular/core';
import {FormControl} from "@angular/forms";

export enum InputHelisaType {
  DEFAULT, IDENTITY, NUMERIC, DOUBLE
}

@Component({
  selector: 'hel-input',
  templateUrl: './input-helisa.component.html',
  styleUrls: ['./input-helisa.component.sass']
})
export class InputHelisaComponent implements OnInit {

  private readonly DECIMAL_SEPARATOR = ',';
  private readonly THOUSAND_SEPARATOR = '.';

  @Input() placeholder = '';
  @Output() setValue: EventEmitter<string> = new EventEmitter<string>();
  @Input() isSearch = false;
  //@Input() inputFormControl: FormControl = new FormControl('');
  @Input() isFocused = false;
  @Input() disabled = false;
  @Input() type: InputHelisaType = InputHelisaType.DEFAULT;


  formControlMask: FormControl = new FormControl('');
  private realValue = '';
  private inputFormReal: FormControl = new FormControl('');

  @ViewChild('inputText') nameInput: ElementRef;

  constructor() { }

  @Input()
  set inputFormControl(formControl: FormControl) {
    this.inputFormReal = formControl;
    this.inputFormReal.valueChanges.subscribe(data => {
      if (this.getMaskedValue(data) != this.formControlMask.value)
        this.change(data);
    });
    this.change(this.inputFormReal.value);
  }

  ngOnInit() {
    if (this.isFocused) {
      this.nameInput.nativeElement.focus();
    }
  }

  search() {
    this.setValue.emit(this.realValue);
  }

  change(event) {
    if (event != null) event = event + '';
    let position = this.nameInput.nativeElement.selectionStart;
    const length = event ? event.length : 0;
    this.realValue = this.getRealValue(event);
    if (this.getMaskedValue(this.realValue) != this.formControlMask.value) {
      this.formControlMask.setValue(this.getMaskedValue(this.realValue));
      position += this.nameInput.nativeElement.value.length - length;
      this.nameInput.nativeElement.selectionStart = position;
      this.nameInput.nativeElement.selectionEnd = position;
    }
    this.inputFormReal.setValue(this.realValue);
  }

  private getMaskedValue(str) {
    if(str == null)
      return str;
    str = str + '';
    if (this.type == InputHelisaType.DEFAULT)
      return str;
    let maskedStr = '';
    if (this.type == InputHelisaType.IDENTITY) {
      for(let i = str.length - 1, j = 0; i >= 0; i--, j++) {
        if(j > 0 && j % 3 == 0)
          maskedStr = this.DECIMAL_SEPARATOR + maskedStr;
        maskedStr = str[i] + maskedStr;
      }
    }
    if (this.type == InputHelisaType.NUMERIC) {
      for(let i = str.length - 1, j = 0; i >= 0; i--, j++) {
        if(j > 0 && j % 3 == 0)
          maskedStr = this.THOUSAND_SEPARATOR + maskedStr;
        maskedStr = str[i] + maskedStr;
      }
    }
    if (this.type == InputHelisaType.DOUBLE) {
      if(str.indexOf(this.DECIMAL_SEPARATOR) >= 0)
        for(let i = str.indexOf(this.DECIMAL_SEPARATOR); i < str.length; i++)
          maskedStr += str[i];
      for(let i = (str.indexOf(this.DECIMAL_SEPARATOR) >= 0?str.indexOf(this.DECIMAL_SEPARATOR):str.length) - 1, j = 0; i >= 0; i--, j++) {
        if(j > 0 && j % 3 == 0)
          maskedStr = this.THOUSAND_SEPARATOR + maskedStr;
        maskedStr = str[i] + maskedStr;
      }
    }
    return maskedStr;
  }

  private getRealValue(str) {
    if(str == null)
      return str;
    str = str + '';
    let realStr = '';
    if (this.type == InputHelisaType.DEFAULT)
      return str;
    if (this.type == InputHelisaType.IDENTITY) {
      for(let i = 0; i < str.length; i++)
        if (str[i].match('[a-zA-Z0-9]'))
          realStr += str[i];
    }
    if (this.type == InputHelisaType.NUMERIC) {
      for(let i = 0; i < str.length; i++)
        if (str[i].match('[0-9]'))
          realStr += str[i];
    }
    if (this.type == InputHelisaType.DOUBLE) {
      let haveDot = false;
      for(let i = 0; i < str.length; i++) {
        if (str[i].match('[0-9]') || ((str[i] == this.DECIMAL_SEPARATOR) && !haveDot))
          realStr += str[i];
        haveDot = haveDot || (str[i] == this.DECIMAL_SEPARATOR);
      }
    }
    return realStr;
  }
}
